*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.link {
  color: inherit;
  text-decoration: none;
}

.list, ul, ol {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
}

input {
  font-family: inherit;
}

a {
  color: #000;
  text-decoration: none;
}

div {
  outline: none;
}

.header-container {
  width: 320px;
  background-color: #84a178;
  margin: 0 auto;
}

.header-nav {
  background-color: #84a178;
}

.header-nav-list {
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  line-height: 39px;
}

.nav-bar-desktop {
  display: none;
}

.nav-bar-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 32px;
  margin-bottom: 110px;
  display: flex;
}

.nav-bar-list .how-its-made {
  color: #fff;
}

.menu-open-btn, .menu-close-btn, .modal-buy-now-menu-close-btn {
  background-color: inherit;
}

.buy-now-btn.tablet-btn {
  display: none;
}

.buy-now-btn {
  text-align: center;
  background-color: #f8d24e;
  border-radius: 10px;
  align-items: center;
  margin: 0 auto;
  padding: 12px 51px 12px 36px;
  font-size: 14px;
  font-weight: 600;
  line-height: 17.07px;
  display: flex;
}

.buy-now-image-box {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  outline: 2px solid #96af8c;
  flex-direction: column;
  justify-content: space-between;
  gap: 72px;
  margin-top: 72px;
  display: flex;
  position: relative;
}

.buy-now-image-box .box-ice-coffee {
  background-color: #efd478;
  outline-color: #efd478;
}

.hamburger-menu {
  visibility: hidden;
  opacity: 0;
  min-width: 100%;
  height: 100vh;
  z-index: 999;
  background-color: #84a178;
  padding: 12px 20px 40px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.hamburger-menu.is-open {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.hamburger-menu-bar {
  justify-content: space-between;
  margin-bottom: 50px;
  display: flex;
}

.mobile-menu {
  justify-content: space-between;
  padding: 12px 20px 0;
  display: flex;
}

.buy-button {
  display: none;
}

@media (min-width: 480px) {
  .header-container {
    min-width: 480px;
  }

  .logo-icon {
    width: 48px;
    height: 44px;
  }
}

@media (min-width: 768px) {
  .header-container {
    min-width: 768px;
  }

  .mobile-menu {
    padding: 20px 32px 0;
  }

  .buy-now-btn.tablet-btn {
    display: inline-block;
  }

  .menu-buttons {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .menu-open-btn {
    text-align: center;
    align-self: center;
    margin-right: 32px;
  }

  .menu-open-btn svg {
    fill: #fff;
  }
}

@media (min-width: 1200px) {
  .header-container {
    min-width: 1200px;
  }

  .logo-icon {
    width: 60px;
    height: 65px;
  }

  .mobile-menu {
    padding: 8px 16px 0;
  }

  .menu-open-btn {
    display: none;
  }

  .nav-bar-desktop {
    display: contents;
  }

  .nav-bar-desktop .nav-bar-list {
    flex-direction: row;
    margin-bottom: 0;
    display: flex;
  }

  .header-nav-list {
    text-align: left;
    color: #fcfcfc;
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
  }

  .header-nav-list:hover, .header-nav-list:focus, .header-nav-list:active {
    color: #f8d24e;
  }
}

.modal-buy-now {
  visibility: hidden;
  opacity: 0;
  width: 280px;
  height: 80%;
  z-index: 1000;
  background-color: #fff;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  flex-direction: column;
  gap: 20px;
  padding: 0 16px 40px;
  transition: opacity;
  display: flex;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.modal-buy-now .menu-close-btn, .modal-buy-now-menu-close-btn {
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal-buy-now .modal-title {
  width: auto;
  height: auto;
  text-align: center;
  margin: 28px 0 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.modal-buy-now.is-open {
  visibility: visible;
  opacity: 1;
}

.product-card-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 84px;
  margin-bottom: 84px;
  display: flex;
}

@media (min-width: 768px) {
  .modal-buy-now {
    width: 568px;
    height: 644px;
    gap: 40px;
  }

  .product-card-list {
    gap: 20px;
  }

  .buy-now-form {
    padding: 0 100px;
  }
}

.product-card-list li {
  width: 132px;
  height: 135px;
}

.image-box {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  outline: 2px solid #96af8c;
  flex-direction: column;
  justify-content: space-between;
  gap: 72px;
  margin-top: 64px;
  display: flex;
  position: relative;
}

.image-box.box-ice-coffee {
  background-color: #efd478;
  outline-color: #efd478;
}

.image-box.box-milkshake {
  outline-color: #d6936d;
}

.product-image {
  border-radius: 50%;
  position: absolute;
  transform: translateY(-45%);
  box-shadow: 0 10px rgba(156, 190, 143, .698), 0 15px 20px rgba(10, 136, 23, .25);
}

.product-image.ice-coffee {
  box-shadow: 0 10px rgba(248, 210, 78, .698), 0 15px 20px rgba(153, 114, 23, .25);
}

.product-image.milkshake {
