}

.buy-now .product-card-title {
  letter-spacing: .04em;
  text-transform: uppercase;
  align-self: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 19.5px;
  position: absolute;
  top: 20px;
  bottom: 20px;
}

.buy-now-form .modal-reusable-form {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .buy-now .product-card-title {
    top: 30px;
  }
}

@media (min-width: 1200px) {
  .buy-now .product-card-title {
    top: -20px;
  }
}

