body {
  margin: 0 auto;
  font-family: Montserrat, sans-serif;
}

.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 480px) {
  .container {
    width: 480px;
    padding: 0 80px;
  }
}

@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1200px) {
  .container {
    min-width: 1200px;
    padding: 0 16px;
  }
}

.section-products {
  background-image: url("ice-cream-background-mobile.58a6717b.jpg");
  background-position: 535px -120px;
  position: relative;
}

.section-products-header {
  text-align: center;
  margin-bottom: 148px;
  padding-top: 60px;
  display: block;
}

.section-products-header-span {
  text-align: center;
  color: #131313;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 800;
  line-height: 1.218;
}

.section-products-header-title {
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: #131313;
  padding-top: 12px;
  font-family: Seymour One, sans-serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.257;
}

.products-grid-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 14.5px;
  display: flex;
}

.product-card-item {
  width: 280px;
  height: 420px;
  border-radius: 24px;
  position: relative;
}

.ice-cream-container {
  background-color: #aec6a5;
  justify-content: center;
  display: flex;
  position: relative;
}

.ice-cream-container:after {
  content: "";
